<template>
  <div style="max-width: 700px; margin: auto">
    <h2 class="my-4 mx-3"> </h2>
    <v-row class="mt-3 mx-3">
      <v-select class="mx-4" label=""
                v-model="sortBy" @change="onChange"
                :items="sortItems" item-text="text" item-value="value" />
      <v-select class="mx-4" label=""
                v-model="sortDir" @change="onChange"
                :items="dirItems" item-text="text" item-value="value" />
    </v-row>
    <h2 class="my-4 mx-3 text-center" v-if="favorites.length === 0">  !</h2>
    <cities-list :cities="itemsOnPage" weather del-btn />
    <div class="text-center mx-3">
      <v-pagination
          v-if="length > 1"
          v-model="page"
          :length="length"
      ></v-pagination>
    </div>
  </div>

</template>

<script>
import CitiesList from "@/components/CitiesList";
import {mapMutations} from 'vuex'
export default {
name: "Favorites",
  components:{
    CitiesList
  },
  data(){
    return {
      itemsCount: 5,
      page: 1,
      sortBy: 'name',
      sortDir: 'asc',
      sortItems:[
        {text: ' ', value: 'name'},
        {text: '  ', value: 'date'},
        {text: ' ', value: 'pos'}
      ]
    }
  },
  computed:{
    dirItems(){
      if (this.sortBy === 'name')
        return [
          {text: '-', value: 'asc'},
          {text: '-', value: 'desc'}
        ]
      else if (this.sortBy === 'date')
        return [
          {text: ' ', value: 'asc'},
          {text: ' ', value: 'desc'}
        ]
      else
        return [
          {text: '  ', value: 'asc'},
          {text: '  ', value: 'desc'}
        ]
    },
    itemsOnPage(){
      let result = []
      this.favorites.forEach((city, index) => {
        if (index < this.page * this.itemsCount && index >= (this.page - 1) * this.itemsCount)
          result.push(city)
      })
      return result
    },
    length(){
      return Math.ceil(this.favorites.length / this.itemsCount)
    }
  },
  methods:{
    ...mapMutations(["sortFavorites"]),
    onChange(){
      this.sortFavorites({type: this.sortBy, dir: this.sortDir})
      this.page = 1
    }
  },
  mounted() {
    this.sortFavorites({type: this.sortBy, dir: this.sortDir})
  }
}
</script>

